@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  font-family: 'Raleway', sans-serif; }

html {
  -ms-overflow-style: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body::-webkit-scrollbar {
  width: 0 !important; }

h1 {
  white-space: pre-line;
  font-size: 1.9rem; }

h5 {
  font-size: 1.1rem; }

.App {
  min-height: 101vh;
  overflow: hidden;
  padding: 5px;
  background: #1a1a1a; }

ul:focus {
  outline: none; }

li:focus {
  outline: none; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

.to-top {
  background: none;
  color: #fefefe;
  border: none;
  width: 100%;
  height: 8%;
  position: absolute;
  top: 1%;
  left: 0%;
  cursor: pointer; }

.to-top-project {
  background: none;
  color: #fefefe;
  border: none;
  width: 100%;
  height: 8%;
  position: absolute;
  top: 1%;
  left: 0%;
  cursor: pointer; }

.to-bottom {
  background: none;
  color: #fefefe;
  border: none;
  width: 100%;
  height: 8%;
  position: absolute;
  bottom: 1%;
  left: 0%;
  cursor: pointer; }

.to-bottom-project {
  background: none;
  color: #fefefe;
  border: none;
  width: 100%;
  height: 10%;
  position: absolute;
  bottom: 0%;
  left: 0%;
  cursor: pointer; }

@media screen and (min-width: 0px) and (max-width: 767px) {
  .to-bottom-project {
    background: none;
    color: #fefefe;
    border: none;
    width: 100%;
    height: 10%;
    position: absolute;
    bottom: -35%;
    left: 0%;
    cursor: pointer; } }

.to-bottom-first {
  background: none;
  color: #fefefe;
  border: none;
  width: 100%;
  height: 8%;
  left: 0;
  position: absolute;
  bottom: 1%;
  left: 0%;
  cursor: pointer; }

.to-first {
  background: none;
  color: #fefefe;
  border: none;
  width: 100%;
  height: 10%;
  position: absolute;
  bottom: 1%;
  left: 0%;
  cursor: pointer; }

.section-1 {
  position: relative;
  width: 100%;
  height: 101vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fefefe;
  background-color: #1a1a1a;
  overflow: auto; }
  .section-1 .section-1-p {
    font-size: 1.6rem; }

.section-2 {
  position: relative;
  height: 101vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fefefe;
  background-color: #1a1a1a;
  overflow: auto; }

.section-3 {
  position: relative;
  height: 101vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fefefe;
  background-color: #1a1a1a;
  overflow: auto; }

.typed-cursor {
  opacity: 1;
  animation: blink 0.6s infinite;
  -webkit-animation: blink 0.6s infinite;
  -moz-animation: blink 0.6s infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.content-wrapper {
  padding: 1rem; }

.project-title {
  margin-bottom: 5px;
  text-align: center;
  font-size: 1.6rem; }

.project-title-p3 {
  margin-bottom: 5px;
  text-align: center;
  font-size: 1.6rem; }

@media screen and (min-width: 0px) and (max-width: 767px) {
  .project-title {
    margin: 200px 0 5px 0;
    text-align: center;
    font-size: 1.6rem; }
  .project-title-p3 {
    margin: 140px 0 5px 0;
    text-align: center;
    font-size: 1.6rem; } }

.sub-title {
  text-align: center;
  padding-bottom: 10px;
  font-size: 1.2rem; }

.embed-responsive {
  margin-bottom: 10px; }

.info {
  text-align: left; }

.content-title {
  padding-top: 10px; }

.content-summary {
  line-height: normal;
  line-height: initial;
  width: 80%; }

.content-list {
  font-family: 'Raleway', sans-serif;
  padding: 2rem; }

.content-list li {
  display: block; }

.btn-wrapper {
  text-align: center; }

button {
  cursor: pointer; }

.btn-info-link {
  margin-right: 10px; }

.content-text-stacks {
  line-height: 1.6; }

@media screen and (min-width: 0px) and (max-width: 533px) {
  .btn-info-link {
    width: 38px;
    overflow: hidden; }
    .btn-info-link .fa {
      margin-right: 20px; } }

